@import url("./components/fonts/Montserrat/Montserrat.css");

:root {
  --navbar-height: 50px;
}

body {
  margin: 0;
  font-family: "Montserrat", "Oxygen", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e0e2e3;
  height: 100vh;
  overflow: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@media only screen and (max-width: 12in) {
  :root {
    --navbar-height: 100px;
  }
  body {
    overflow: scroll;
  }

  #footer {
    position: static;
    flex-direction: column;
    gap: 3px;
  }

}
