.top-part {
  display: flex;
  justify-content: center;
  opacity: 0;
  transition: opacity 1s ease;
  /* margin-top: calc(var(--navbar-height)); */
}
.top-image {
  width: 100vw; /* Imposta la larghezza al 100% della larghezza della finestra del browser */
  height: 100vh; /* Imposta l'altezza al 100% dell'altezza della finestra del browser */
  object-fit: cover; /* Assicura che l'immagine copra l'intera area mantenendo le proporzioni */
}
.loaded {
  opacity: 1;
}

.top-part-text {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 9;
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-text {
  padding: 40px;
  color: #000000;
  text-shadow: 1px 1px 3px rgba(255, 255, 255, 1);
  margin: auto;
  font-size: 4rem;
  font-weight: bolder;
  transform: translateX(-120%);
  transition: transform 1s ease-in-out;
  text-align: center;
  margin: 0;
}
.top-text > h3 {
  margin: 0;
}
.top-text > div {
  margin-top: 10%;
}
.top-text > div > p {
  font-size: 2rem;
}
.top-text.show {
  transform: translateX(0);
}
.fade-out {
  opacity: 0;
  transition: opacity 0.4s ease-out;
}
.fade-in {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}
.fade-out-img {
  opacity: 0.3;
  transition: opacity 0.4s ease-out;
}
.fade-in-img {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}
@media only screen and (max-width: 12in) {
  .top-text > h3 {
    font-size: xx-large;
    margin: 0;
  }
  .top-text > div >h6{
    font-size: large;
  }
  .top-text > div > p {
    font-size: 1rem;
  }
  .logo-home{
    width: 26% !important;
  }
}
