.navbar-container {
  width: 100vw;
  height: 50px;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: wheat;
  position: fixed;
  top: 0;
  z-index: 999;
  gap: 10px;
}
.navbar-container a {
  text-decoration: none;
  color: white;
  font-size: 16px;
  margin: 0 10px;
}

/* .navbar-container a:hover {
  text-decoration: underline;
} */
.navbar-container a:not(#imglogo) {
  position: relative;
  text-decoration: none;
}

.navbar-container a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #ffffff;
  transition: width 0.2s ease;
}

.navbar-container a:hover::after {
  width: 100%;
}

/* .selected {
  text-decoration: underline !important;
  -webkit-text-decoration: underline !important;
} */
.navbar-container a.selected::after {
  width: 100%;
}

.logo-img-navbar {
  width: 50px;
  border-radius: 15px;
}
/* Stili per la versione mobile */
@media only screen and (max-width: 600px) {
  .navbar-container {
    height: 70px; /* Altezza diversa per la versione mobile */
    gap: 5px; /* Riduci lo spazio tra i link per la versione mobile */
  }

  .navbar-container a {
    font-size: 14px; /* Riduci la dimensione del font per la versione mobile */
  }

  .logo-img-navbar {
    width: 40px; /* Riduci le dimensioni del logo per la versione mobile */
  }
}
